<template>
  <section class="card">
    <div class="card-header">
      <div class="utils__title">
        <strong>Send SMS</strong>
      </div>
    </div>
    <div class="card-body">
      <div :class="$style.addPost">
        <a-form class="mt-3" :form="form" @submit="handleSubmit">
          <div class="form-group">
            <div class="form-group">
              <a-form-item label="Phone Numbers  (Required unless if sending to group.)">
                <a-textarea style="height: 200px" v-decorator="['phone',{ rules: [{ required: true, message: 'Phone numbers are required' }]}]" placeholder="Phone Numbers" />
              </a-form-item>
            </div>
            <div class="form-group">
              <a-form-item label="Text Message  (Required)">
                <a-textarea style="height: 200px" v-decorator="['message',{ rules: [{ required: true, message: 'Message is required' }]}]" placeholder="Message" />
              </a-form-item>
            </div>
            <a-form-item>
              <div :class="$style.submit">
                <span class="mr-3">
                  <a-button type="primary" htmlType="submit">Save sms</a-button>
                </span>
                <a-button @click="handleReset()" type="danger">Discard</a-button>
              </div>
            </a-form-item>
          </div>
        </a-form>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'index',
  data() {
    return {
      form: this.$form.createForm(this),
    }
  },
  methods: {
    handleReset() {
      this.form.resetFields()
    },
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.$nprogress.start()
          const requestPayload = {
            phone_numbers: values.phone,
            message: values.message,
          }

          this.axios.post('msg/send', requestPayload)
            .then(response => {
              this.handleReset()
              this.$nprogress.done()
              console.log('stats', response.status)
              if (response.status === 200) {
                this.$notification['success']({
                  message: 'Sms Messaging',
                  description: 'You have successfully Sent a Bulk SMS ',
                })
              }
            })
            .catch(error => {
              console.log('error', error.response.data.data)
              if (error.response.status === 401) {
                this.$notification['warning']({
                  message: 'Sms Messaging',
                  description: 'Sorry all fields are important ',
                })
              } else {
                this.$notification['warning']({
                  message: 'Sms Messaging',
                  description: 'Sorry an error occurred.',
                })
              }
            })
        }
      })
    },
  },
}
</script>

<style lang="scss" module>
  @import "./style.module.scss";
</style>
